<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					 <span class="circle_blue"></span> 
					<span class="name">支出-其他</span>
				</div>
			</div>
			<!--新增支出模块-->
			<div class="table" v-if="is_add_page == 1">
				<el-table :data="add_info" border style="width: 100%">
					<el-table-column prop="unit" label="收入主体类别" width="200">
						<template slot-scope="scope">
							<el-select v-model="scope.row.fund_type" @change="chooseFundType" clearable placeholder="请选择">
							    <el-option v-for="item in fund_main_type_list" :key="item.value" :label="item.label" :value="item.value">
							    </el-option>
							</el-select>
						</template>
					</el-table-column>
					<el-table-column prop="unit" label="收入主体名称" width="230">
						<template slot-scope="scope">
							<el-select v-model="scope.row.fund_id" @change="chooseFundName" clearable placeholder="请选择" filterable>
							    <el-option v-for="item in fund_list" :key="item.id" :label="item.name" :value="item.id">
							    </el-option>
							</el-select>
						</template>
					</el-table-column>
					<el-table-column prop="date" label="日期" width="180">
						<template slot-scope="scope">
							<el-date-picker v-model="scope.row.date" @change="chooseDate($event)" type="date" value-format="yyyy-MM-dd" placeholder="选择日期" :editable='false'>
							</el-date-picker>
						</template>
					</el-table-column>
					<el-table-column prop="event" label="事件" width="250">
					</el-table-column>
					
					<el-table-column prop="output" label="金额（万元）" width="180">
						<template slot-scope="scope">
							<el-input placeholder="请输入内容" v-model="scope.row.output" clearable>
							</el-input>
						</template>
					</el-table-column>
					
					<el-table-column prop="unit" label="币种" width="180">
						<template slot-scope="scope">
							{{ unit == 1?'人民币':'美元' }}
						</template>
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="120">
						<template slot-scope="scope">
							<el-button  type="text" size="small" @click="addSave(scope.row)">保存</el-button>
							<el-button  type="text" size="small" @click="editCancel(scope.row)">取消</el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
			
			<!--查看支出模块-->
			<div class="table" v-else>
				<el-table :data="tableData" border style="width: 100%">
					<el-table-column prop="" label="收入主体类别" width="200">
						<template slot-scope="scope">
							<div class="" v-if="is_editable">
								<el-select v-model="edit_fund_type" @change="chooseFundType" clearable placeholder="请选择">
								    <el-option v-for="item in fund_main_type_list" :key="item.value" :label="item.label" :value="item.value">
								    </el-option>
								</el-select>
							</div>
							<div class="" v-else>
								{{ scope.row.fund_type_name }}
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="" label="收入主体名称" width="230">
						<template slot-scope="scope">
							<div class="" v-if="is_editable">
								<el-select v-model="edit_fund_id" @change="chooseFundName" clearable placeholder="请选择">
								    <el-option v-for="item in fund_list" :key="item.id" :label="item.name" :value="item.id">
								    </el-option>
								</el-select>
							</div>
							<div class="" v-else>
								{{ scope.row.fund_name }}
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="date" label="日期" width="200">
						<template slot-scope="scope">
							<div class="" v-if="is_editable">
								<el-date-picker v-model="edit_date" @change="chooseDate($event)" type="date" value-format="yyyy-MM-dd" placeholder="选择日期" :editable='false'>
								</el-date-picker>
							</div>
							<div class="" v-else>
								{{ scope.row.date }}
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="event" label="事件" width="200">
						<template slot-scope="scope">
							<div class="" v-if="is_editable">
								{{ edit_event }}
							</div>
							<div class="" v-else>
								{{ scope.row.event }}
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="output" label="金额（万元）" width="200">
						<template slot-scope="scope">
							<div class="" v-if="is_editable">
								<el-input placeholder="请输入内容" v-model="edit_output" clearable>
								</el-input>
							</div>
							<div class="" v-else>
								{{ scope.row.output }}
							</div>
						</template>
					</el-table-column>
					
					<el-table-column prop="unit" label="币种" width="180">
						<template slot-scope="scope">
							{{ unit == 1?'人民币':'美元' }}
						</template>
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="120" v-if="is_delete">
						<template slot-scope="scope">
							<el-button v-if="is_editable" type="text" size="small" @click="editSave(scope.row)">保存</el-button>
							<el-button v-if="is_editable" type="text" size="small" @click="editCancel(scope.row)">取消</el-button>
							<el-button v-if="!is_editable" type="text" size="small" @click="edit(scope.row)">编辑</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
			</div>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn,getSelectedItem } from '../../config/mUtils'
	import { outputFofGet,outputPetFundGet } from '../../config/apiSj'
	import { addMotherPayoutCashApi,editMotherPayoutCashApi,getMotherPayoutCashApi,addSelfPayoutCashApi,editSelfPayoutCashApi,getSelectListMotherPayoutCashApi,getNameApi,getEventByDateApi } from '../../config/api'
	
	export default {
		data() {
			return {
				id: '',
				type: 1,//1代表母基金模块；2代表自管基金模块

				unit:'',//该基金对应的币种
				count:'',
				n: '',//代表事件中的第几次支出
				add_info: [
					{
						event: '',
						output: '',
						date: '',
						unit: '',
						fund_type: '',
						fund_id: '',
						event_count: '',
					}
				],
				tableData: [],
				// 编辑时数据开始
				edit_event: '',
				edit_output: '',
				edit_date: '',
				edit_unit: '',
				edit_fund_id: '',
				edit_fund_type: '',
				edit_event_count: '',
				// 编辑时数据结束
				coin_list: [
					{
						label: '人民币',
						value: 1
					},
					{
						label: '美元',
						value: 2
					}
				],
				is_add_page: 1,
				is_editable: 0,
				is_delete:0,
				fund_main_type_list: [
					{
						value: 1,
						label: '母基金'
					},
					{
						value: 2,
						label: '自管基金'
					},
				],
				fund_list: [],
				all_fund_list: [],
			}
		},
		created() {
			this.is_add_page = this.$route.query.is_add_page;
			this.is_delete = this.$route.query.is_delete;
			this.id = this.$route.query.id;
			this.type = this.$route.query.type;
			this.count = this.$route.query.count;
			this.getSelectListData();
			this.getName();
			if(!this.is_add_page){
				this.getDeatilData();
			}
		},
		computed: {
	
		},
		mounted() {},
		methods: {
			getName(){
				getNameApi({
					id: this.id,
					type: this.type
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.unit = res.data.payload.data.unit;
					} else {
						dialogConfirm('info', res.data.message);
					}
				})
			},

			getSelectListData(){
				getSelectListMotherPayoutCashApi({
					fof_id: this.id,
					difference_sign: this.type
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.all_fund_list = res.data.payload.data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			chooseFundType(e){
				this.add_info[0].fund_id = '';
				this.edit_fund_id = '';
				var param = String(e)
				this.fund_list = this.all_fund_list[param];
			},
			chooseFundName(e){
				if (e) {
					this.getEventCountData()
				} else {
					this.add_info[0].event = '';
					this.add_info[0].event_count = '';
					this.edit_event = '';
					this.edit_event_count = '';
				}
			},
			chooseDate(date){
				if (!date) {
					this.add_info[0].event = '';
					this.add_info[0].event_count = '';
					this.edit_event = '';
					this.edit_event_count = '';
					return
				}
				this.getEventCountData()
			},
			getEventCountData(){
				if (this.type == 1) {
					var type = 2;
				} else {
					var type = 3;
				}
				if (this.is_add_page) {
					var date = this.add_info[0].date;
					var fund_id = this.add_info[0].fund_id;
					var event_id = '';
					if (this.add_info[0].fund_type == 1) {
						var fund_type = 2;
					} else if(this.add_info[0].fund_type == 2){
						var fund_type = 3;
					} else {
						var fund_type = '';
					}
				} else {
					var date = this.edit_date;
					var fund_id = this.edit_fund_id;
					var event_id = this.tableData[0].id;
					if (this.edit_fund_type == 1) {
						var fund_type = 2;
					} else if(this.edit_fund_type == 2){
						var fund_type = 3;
					} else {
						var fund_type = '';
					}
				}

				getEventByDateApi({
					type: type,
					id: this.id,
					cash_type: 6,
					date: date,
					fund_type: fund_type,
					fund_id: fund_id,
					event_id: event_id
				}).then(res => {
					if(res.data.msgNo == 1) {
						if (this.is_add_page) {
							this.add_info[0].event = res.data.payload.event_name;
							this.add_info[0]['event_count'] = res.data.payload.event_count;
						} else{
							this.edit_event = res.data.payload.event_name;
							this.edit_event_count = res.data.payload.event_count;
						}
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			
			
			
			
			
			
			
			
			addSave(){
				if (this.type == 1) {
					addMotherPayoutCashApi({
						event: this.add_info[0].event,
						output: this.add_info[0].output,
						date: this.add_info[0].date,
						unit: this.unit,
						fof_id: this.id,
						fund_type: this.add_info[0].fund_type,
						fund_id: this.add_info[0].fund_id,
						event_count: this.add_info[0].event_count
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.clearData()
							this.$router.go(-1);
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				} else{
					addSelfPayoutCashApi({
						event: this.add_info[0].event,
						output: this.add_info[0].output,
						date: this.add_info[0].date,
						// unit: this.add_info[0].unit,
						unit: this.unit,
						petfund_id: this.id,
						fund_type: this.add_info[0].fund_type,
						fund_id: this.add_info[0].fund_id,
						event_count: this.add_info[0].event_count
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.clearData()
							this.$router.go(-1);
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}
			},
			
			
			// 查看页面功能模块
			edit(e){
				this.is_editable = 1;
				var param = String(e.fund_type);
				this.fund_list = this.all_fund_list[param];
				this.makeData(e)
			},
			makeData(e){
				this.edit_event = e.event;
				this.edit_output = e.output;
				this.edit_date = e.date;
				this.edit_unit = e.unit;
				this.edit_fund_type = e.fund_type;
				this.edit_fund_id = e.fund_id;
			},
			editSave(e){
				if (this.type == 1) {
					editMotherPayoutCashApi({
						event: this.edit_event,
						output: this.edit_output,
						date: this.edit_date,
						// unit: this.edit_unit,
						unit: this.unit,
						fof_id: this.id,
						id: e.id,
						fund_type: this.edit_fund_type,
						fund_id: this.edit_fund_id,
						event_count: this.edit_event_count
					}).then(res => {
						// console.log(res);
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.is_editable = 0;
							this.getDeatilData();
							//还需要请求数据接口刷新页面
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				} else{
					editSelfPayoutCashApi({
						event: this.edit_event,
						output: this.edit_output,
						date: this.edit_date,
						// unit: this.edit_unit,
						unit: this.unit,
						petfund_id: this.id,
						id: e.id,
						fund_type: this.edit_fund_type,
						fund_id: this.edit_fund_id,
						event_count: this.edit_event_count
					}).then(res => {
						// console.log(res);
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.is_editable = 0;
							this.getDeatilData();
							//还需要请求数据接口刷新页面
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}
			},
			editCancel(){
				this.is_editable = 0;
				this.clearData()
			},
			clearData(){
				this.edit_event = '';
				this.edit_output = '';
				this.edit_date = '';
				this.edit_unit = '';
				this.edit_fund_type = '';
				this.edit_fund_id = '';
				this.edit_event_count = '';
				this.add_info[0].event = '';
				this.add_info[0].output = '';
				this.add_info[0].date = '';
				this.add_info[0].unit = '';
				this.add_info[0].fund_id = '';
				this.add_info[0].fund_type = '';
				this.add_info[0].event_count = '';
			},
			//获取自管基金，母基金详情
			getDeatilData(){
				if (this.type == 1) {
					outputFofGet({
						id:this.count
					}).then(res => {
						if(res.data.msgNo == 1) {
							this.n = res.data.payload.data.event_count;
							let data = res.data.payload.data;
							this.tableData = [];
							this.tableData.push(data);
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}else{
					outputPetFundGet({
						id:this.count
					}).then(res => {
						if(res.data.msgNo == 1) {
							this.n = res.data.payload.data.event_count;
							let data = res.data.payload.data;
							this.tableData = [];
							this.tableData.push(data);
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.body {
		padding: 0 0px 30px 0px;
	}
	
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}
</style>


